
.main {
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
}
.container {
  color: #4D5656;
  max-height: 100%;
  overflow: auto;
}

h3 {
  margin: .5em 0;
}

.spawner {
  flex: 0 0 30%;
  padding: 5px 20px;
  background: white;
  border-radius: 5px;
  box-shadow: 0 0 5px gray;
  margin: 5px;
}

input, select {
  color: #4D5656;
  padding: 5px;
  font-size: .9em;
  border: 1px solid #ccc;
  outline-style: none;
  border-radius: 5px;
  margin: 2px;
}

input:focus{
  border-color: #66afe9;
  outline: 0;
  -webkit-box-shadow: inset 0 1px 1px rgba(0,0,0,.075),0 0 5px rgba(102,175,233,.6);
  box-shadow: inset 0 1px 1px rgba(0,0,0,.075),0 0 5px rgba(102,175,233,.6)
}

input[type=checkbox] {
  width: 15px;
  height: 15px;
  vertical-align: middle;
}

label {
  font-size: .9em;
}

.button {
  background: #3498DB;
  border-radius: 5px;
  width: auto;
  color: white;
  padding: 8px 16px;
  margin: 5px auto;
  text-align: center;
}

.button:hover {
  background: #5DADE2;
}

.button:active {
  background: #2E86C1;
}
